export default {
	data() {
		return {
			currentNum:1,
			stu:null,
			dataList:null,
			pageType:null
		}
	},
	methods: {
		next()
		{
			this.currentNum++;
			this.visitRemindDate()
			
			
		},
		prevent()
		{
			this.currentNum--;
			this.visitRemindDate()
		},
		
		visitRemindDate()
		{
			console.log(this.pageType)
			uni.request({
				url:this.serverUrl + '/stu/visit_remind/' + this.pageType + '/' + this.currentNum + '?limit=1',
				method:'GET',
				data:{},
				success: visitTimeDate => {
					console.log(visitTimeDate)
					this.stu = visitTimeDate.data[0]
				},
				fail: () => {},
				complete: () => {}
				
			})
		},
	},
	computed:{
		hasPrevent:function(){
			return{
				'bg-red': true
			}
		}
	},
	onShow(param) {
		var me = this;
		this.visitRemindDate()
		
		
		
	},
	onLoad(param)
	{
		console.log("load... type:" + param.type)
		this.pageType = param.type
		
	}
	
}
